	font-size: 0.875rem;
	font-weight: 400;
	line-height: 1.5;
	color: #686868;
	background-color: $white;
	background-clip: padding-box;
	border: 1px solid $gray-300;
	border-radius: 7px;
	transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  
	&::-ms-expand {
	  background-color: transparent;
	  border: 0;
	}
  
	height: 38px;
	border-radius: 7px;
  
	&:focus {
	  border-color: #c4cdd7;
	  box-shadow: none;
	}
  }
  
