		display: flex;
		flex: 0 0 auto;
		flex-flow: row wrap;
		align-items: center;
		margin-bottom: 0;
	  }
  
	  .form-control {
		display: inline-block;
		width: auto;
		vertical-align: middle;
	  }
  
	  .form-control-plaintext {
		display: inline-block;
	  }
  
	  .input-group, .custom-select {
		width: auto;
	  }
  
	  .form-check {
		display: flex;
		align-items: center;
		justify-content: center;
		width: auto;
		padding-left: 0;
	  }
  
	  .form-check-input {
		position: relative;
		flex-shrink: 0;
		margin-top: 0;
		margin-right: 0.25rem;
		margin-left: 0;
	  }
  
	  .custom-control {
		align-items: center;
		justify-content: center;
	  }
  
	  .custom-control-label {
		margin-bottom: 0;
	  }
	}
  }
  
  /*--- FORM ELEMENTS ---*/
  /*--- PARSELY ---*/
  
  .morris-hover.morris-default-style {
	border-radius: 0;
  }
  
  input.parsley-error:focus, textarea.parsley-error:focus, input.parsley-success:focus, textarea.parsley-success:focus {
	box-shadow: none !important;
  }
  
  .parsley-checkbox {
	&.parsley-error {
	  display: inline-block;
	  padding: 10px;
	  border: 1px solid $danger;
	}
  
	&.parsley-success {
	  display: inline-block;
	  padding: 10px;
	  border: 1px solid $success;
	}
  }
  
  .parsley-errors-list {
	color: $danger;
	list-style: none;
	font-size: 12px;
	padding: 0;
	margin-bottom: 0;
  
	li {
	  margin: 5px 0 0;
	}
  }
  
  .parsley-style-1 {
	.parsley-input {
	  &.parsley-error .form-control {
		background-color: #fef9f9;
		border-color: $danger;
		border-bottom-width: 0;
		border-bottom-right-radius: 0;
		border-bottom-left-radius: 0;
	  }
  
	  &.parsley-success .form-control {
		border-color: $success;
	  }
	}
  
	.parsley-checkbox.parsley-error {
	  border: 1px solid $danger;
	  background-color: #fef9f9;
	  border-top-left-radius: 2px;
	  border-top-right-radius: 2px;
	}
  
	.parsley-select.parsley-error .select2-container--default .select2-selection--single {
	  background-color: #fef9f9;
	  border-bottom-right-radius: 0;
	  border-bottom-left-radius: 0;
	}
  
	.parsley-errors-list.filled {
	  background-color: $danger;
	  color: $white;
	  padding: 7px 10px;
	  border-bottom-right-radius: 2px;
	  border-bottom-left-radius: 2px;
  
	  li:first-child {
		margin-top: 0;
	  }
	}
  }
  
  /*--- PARSELY ---*/
  /* Radio Box */
  
  .rdiobox {
	font-weight: normal;
	position: relative;
	display: block;
	line-height: 18px;
  
	span {
	  padding-left: 13px;
  
	  &:before, &:after {
		line-height: 18px;
		position: absolute;
	  }
  
	  &:before {
		content: '';
		width: 16px;
		height: 16px;
		background-color: $white;
		border: 1px solid #c4cdd7;
		border-radius: 50px;
		top: 2px;
		left: 0;
	  }
  
	  &:after {
		content: '';
		width: 6px;
		height: 6px;
		background-color: $white;
		border-radius: 50px;
		top: 7px;
		left: 5px;
		display: none;
	  }
  
	  &:empty {
		padding-left: 0;
		width: 16px;
		display: block;
	  }
	}
  
	input[type='radio'] {
	  opacity: 0;
	  margin: 0;
  
	  &:checked + span {
		&:before {
		  border-color: transparent;
		  background-color: $primary;
		}
  
		&:after {
		  display: block;
		}
	  }
  
	  &[disabled] + span {
		opacity: .75;
  
		&:before, &:after {
		  opacity: .75;
		}
	  }
	}
  }
  
  .rdiobox-inline {
	display: inline-block;
  }
  
  .form-group-rdiobox {
	display: flex;
	align-items: center;
  
	.rdiobox {
	  margin-bottom: 0;
  
	  + .rdiobox {
		margin-left: 30px;
	  }
  
	  span {
		padding-left: 0;
	  }
	}
  }
  
  .main-form-group {
	padding: 12px 15px;
	border: 1px solid $gray-300;
  
	&.focus {
	  border-color: #b4bdce;
	  box-shadow: 0 0 0 2px rgba(0, 110, 255, 0.16);
	}
  
	.form-label {
	  font-size: 13px;
	  margin-bottom: 2px;
	  font-weight: 400;
	  color: #c4cdd7;
	}
  
	.form-control {
	  padding: 0;
	  border-width: 0;
	  height: 25px;
	  color: $default-color;
	  font-weight: 500;
  
	  &:focus {
		box-shadow: none;
	  }
	}
  }