/*--- BADGES ---*/

.badge {
	display: inline-block;
	padding: 3px 6px;
	font-size: 12px;
	font-weight: 400;
	line-height: 1;
	text-align: center;
	white-space: nowrap;
	vertical-align: baseline;
	border-radius: 3px;
	transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  
  @media (prefers-reduced-motion: reduce) {
	.badge {
	  transition: none;
	}
  }
  
  a.badge {
	&:hover, &:focus {
	  text-decoration: none;
	}
  }
  
  .badge:empty {
	display: none;
  }
  
  .btn .badge, .sp-container button .badge {
	position: relative;
	top: -1px;
  }
  
  .badge-pill {
	padding-right: 0.6em;
	padding-left: 0.6em;
	border-radius: 10rem;
  }
  
  .badge-primary {
	color: $white;
	background-color: $primary;
  }
  
  a.badge-primary {
	&:hover {
	  color: $white;
	  background-color: #0040ff;
	}
  
	&:focus {
	  color: $white;
	  background-color: #0040ff;
	  outline: 0;
	  box-shadow: 0 0 0 0.2rem rgba(102, 75, 196, 0.5);
	}
  
	&.focus {
	  outline: 0;
	  box-shadow: 0 0 0 0.2rem rgba(102, 75, 196, 0.5);
	}
  }
  
