  }
  
  .border-bottom {
	border-bottom: 1px solid $gray-300 !important;
  }
  
  .border-left {
	border-left: 1px solid $gray-300 !important;
  }
  
  .border-0 {
	border: 0 !important;
  }
  
  .border-top-0 {
